import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import "./stylesheets/profile.css";

class Profile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: null,
      user_id: props.location.pathname.replace("/profile/", ""),
      username: "",
      email: "",
      firstname: "",
      lastname: "",
      gamesPlayed: null,
      image: null,
      totalScore: null,
      totTime: null,
      totQ: null,
      avgTime: null,
      percentCorrect: null,
      questionsCorrect: null,
      title: null
    };
    this.getUser = this.getUser.bind(this);
    this.getScoreDocs = this.getScoreDocs.bind(this);
    this.getTotalScore = this.getTotalScore.bind(this);
    this.reducerScore = this.reducerScore.bind(this);
    this.reducerTime = this.reducerTime.bind(this);
    this.reducerQuestions = this.reducerQuestions.bind(this);
    this.reducerCorrect = this.reducerCorrect.bind(this);
    this.getTotTime = this.getTotTime.bind(this);
    this.getTotQuestions = this.getTotQuestions.bind(this);
    this.getQuestionCorrect = this.getQuestionCorrect.bind(this);
  }

  componentDidMount() {
    this.mounted = true;
    this.getUser();
    this.getScoreDocs();
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });

    this.canceler = this.props.dbScore
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getScoreDocs();
      });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          _id: this.state.user_id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          user: rows,
          username: rows[0].username,
          email: rows[0].email,
          firstname: rows[0].firstname,
          lastname: rows[0].lastname,
          image: rows[0].image,
          title: rows[0].title
        });
      })
      .catch(err => {
        console.log(err);
      });
  }

  async getScoreDocs() {
    await this.props.dbScore
      .find({
        selector: {
          user: this.state.user_id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          scoreDocs: rows,
          gamesPlayed: rows.length
        });
        console.log(this.state.scoreDocs);
      })
      .catch(err => {
        console.log(err);
      });
    this.getTotalScore();
    this.getTotTime();
    this.getTotQuestions();
    this.getQuestionCorrect();
  }

  reducerScore = (accum, item) => {
    if (accum.score == NaN || item.score == NaN) {
      return;
    } else {
      return Number(accum) + Number(item.score);
    }
  };

  reducerTime = (accum, item) => {
    if (accum.time == NaN || item.time == NaN) {
      return;
    } else {
      return Number(accum) + Number(item.time);
    }
  };

  reducerQuestions = (accum, item) => {
    if (accum.questions == NaN || item.questions == NaN) {
      return;
    } else {
      return Number(accum) + Number(item.questions);
    }
  };

  reducerCorrect = (accum, item) => {
    if (accum.correct == NaN || item.correct == NaN) {
      return;
    } else {
      return Number(accum) + Number(item.correct);
    }
  };

  getTotalScore() {
    const total = Number(this.state.scoreDocs.reduce(this.reducerScore, 0));
    this.setState({
      totalScore: total
    });
  }

  getTotTime() {
    const total = Number(this.state.scoreDocs.reduce(this.reducerTime, 0));
    this.setState({
      totTime: total
    });
  }

  getTotQuestions() {
    const total = Number(this.state.scoreDocs.reduce(this.reducerQuestions, 0));
    this.setState({
      totQ: total,
      avgTime: this.state.totTime / total
    });
  }

  getQuestionCorrect() {
    const total = Number(this.state.scoreDocs.reduce(this.reducerCorrect, 0));
    this.setState({
      questionsCorrect: total,
      percentCorrect: (total / this.state.totQ) * 100
    });
  }

  render() {
    if (this.state.user === null) {
      return null;
    } else {
      return (
        <div>
          <Header
            admin={this.props.location}
            game={"/playHome/" + this.state.user[0].parentClient}
            profile={"/profile/" + this.state.user_id}
          />
          <Link
            className="info back"
            to={"/playHome/" + this.state.user[0].parentClient}
          >
            <h3>&larr; Back to Games</h3>
          </Link>
          <div className="profile_top">
            {/**<img alt="profile_img" src={require("./images/profile.png")} />**/}
            <div className="profile_top_name_role">
              <p className="profile_name">
                {this.state.firstname} {this.state.lastname}{" "}
              </p>
              <p className="profile_role"> {this.state.title} </p>
            </div>
            <div className="profile_top_email">
              <p className="profile_top_label">EMAIL </p>
              <p className="profile_top_text"> {this.state.email} </p>
            </div>
            <div className="profile_top_user">
              <p className="profile_top_label">USERNAME </p>
              <p className="profile_top_text"> {this.state.username} </p>
            </div>
            <button className="edit_profile">
              <p className="edit_profile_text">Edit Profile</p>{" "}
            </button>
          </div>
          <div className="profile_score_div">
            {/** all time score **/}
            <div className="profile_allTime">
              <p className="profile_allTime_label">All Time Score</p>
              <p className="profile_allTime_text">{this.state.totalScore}</p>
            </div>
            <div className="allTime_except">
              <div className="profile_totGames">
                {/** number of games played **/}
                <p className="profile_label">Total # of Games Played</p>
                <p className="profile_text">{this.state.gamesPlayed}</p>
              </div>
              <div className="profile_avgTime">
                {/** average time to answer **/}
                <p className="profile_label">Average Time to Answer</p>
                <p className="profile_text">{Math.round(this.state.avgTime)} sec</p>
              </div>
              <div className="profile_percent">
                {/** percent answered correctly **/}
                <p className="profile_label">Percentage Answered Correctly</p>
                <p className="profile_text">{Math.round(this.state.percentCorrect)}%</p>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}
export default Profile;
