import React, { Component } from "react";
import { useContext } from "react";
import { FaAngleDown } from "react-icons/fa";
import { RiAddCircleLine } from "react-icons/ri";
import { Link } from "react-router-dom";
import AddClient from "./addClient";
import ClientCard from "./clientCard";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class ClientsClass extends Component {
  constructor(props) {
    super(props);
    this.openModal = this.openModal.bind(this);
    this.getClient = this.getClient.bind(this);
    this.getUser = this.getUser.bind(this);
    this.sortByEmail = this.sortByEmail.bind(this);
    this.sortByNameLast = this.sortByNameLast.bind(this);
    this.state = {
      clients: null,
      inactiveClients: null,
      userData: null,
      showModal: false,
      nameClient: "",
      nameFirst: "",
      nameLast: "",
      email: "",
      phone: "",
      title: "",
      street: "",
      town: "",
      state: "",
      zip: "",
      billingStreet: "",
      billingTown: "",
      billingState: "",
      billingZip: ""
    };
  }

  componentDidMount = () => {
    this.mounted = true;
    this.getClient();
    this.getUser();
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getClient();
      });

    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  };

  componentDidUpdate() {
    document.body.style.background = "#F3F3F3";
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  getClient = () => {
    this.setState({
      clients: null,
      inactiveClients: null
    });
    this.props.dbClient
      .allDocs({
        include_docs: true
      })
      .then(res => {
        const rows = res.rows;
        var activeClients = [];
        var inactiveClients = [];
        activeClients = rows.map(row => row.doc);
        console.log(activeClients[0].activeClient);
        for (var i = 0; i < activeClients.length; i++) {
          if (activeClients[i].activeClient === false) {
            inactiveClients.push(activeClients.splice(i, 1));
          }
        }
        this.setState({
          clients: activeClients,
          inactiveClients: inactiveClients
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  openModal(e) {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  }

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeNameClient = e => {
    this.setState({
      nameClient: e
    });
  };

  onChangeNameFirst = e => {
    this.setState({
      nameFirst: e
    });
  };

  onChangeNameLast = e => {
    this.setState({
      nameLast: e
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e
    });
  };

  onChangePhone = e => {
    this.setState({
      phone: e
    });
  };

  onChangeTitle = e => {
    this.setState({
      title: e
    });
  };

  onChangeStreet = e => {
    this.setState({
      street: e
    });
  };

  onChangeTown = e => {
    this.setState({
      town: e
    });
  };

  onChangeState = e => {
    this.setState({
      state: e
    });
  };

  onChangeZip = e => {
    this.setState({
      zip: e
    });
  };

  onChangeBillingStreet = e => {
    this.setState({
      billingStreet: e
    });
  };

  onChangeBillingTown = e => {
    this.setState({
      billingTown: e
    });
  };

  onChangeBillingState = e => {
    this.setState({
      billingState: e
    });
  };

  onChangeBillingZip = e => {
    this.setState({
      billingZip: e
    });
  };

  sortByEmail() {
    if (this.state.clients) {
      var sorted = this.state.clients.slice();
      sorted.sort((a, b) => a.email.localeCompare(b.email));
      this.setState({
        clients: sorted
      });
    }
  }

  sortByNameLast() {
    if (this.state.clients) {
      var sorted = this.state.clients.slice();
      sorted.sort((a, b) => a.nameLast.localeCompare(b.nameLast));
      this.setState({
        clients: sorted
      });
    }
  }

  clientList() {
    return this.state.clients.map((client, i) => (
      <ClientCard
        client={client}
        key={i}
        dbClient={this.props.dbClient}
        dbTeam={this.props.dbTeam}
        color={"#07E0E0"}
        inactive={false}
      />
    ));
  }

  inactiveClientList() {
    return this.state.inactiveClients.map((client, i) => (
      <ClientCard
        client={client[0]}
        key={i}
        dbClient={this.props.dbClient}
        dbTeam={this.props.dbTeam}
        color={"#999"}
        inactive={true}
      />
    ));
  }

  render() {
    if (this.state.clients === null) {
      return null;
    } else if (this.state.userData) {
      if (this.state.userData.admin) {
        return (
          <div>
            <Header admin={"/clients"} game={null} profile={"/profile/"+this.state.userData._id} />
            <div className="info add-container add-container-client">
              <h3
                style={{
                  fontWeight: "400",
                  fontSize: "48px"
                }}
              >
                Clients
              </h3>
              <button className="add-input" onClick={this.openModal.bind(this)}>
                <span>
                  <RiAddCircleLine size={30} color={"#07E0E0"} />
                </span>{" "}
                Add Client
              </button>
              <AddClient
                onClose={this.showModal}
                show={this.state.showModal}
                onChangeNameClient={this.onChangeNameClient}
                onChangeNameFirst={this.onChangeNameFirst}
                onChangeNameLast={this.onChangeNameLast}
                onChangeEmail={this.onChangeEmail}
                onChangePhone={this.onChangePhone}
                onChangeTitle={this.onChangeTitle}
                onChangeStreet={this.onChangeStreet}
                onChangeTown={this.onChangeTown}
                onChangeState={this.onChangeState}
                onChangeZip={this.onChangeZip}
                onChangeBillingStreet={this.onChangeBillingStreet}
                onChangeBillingTown={this.onChangeBillingTown}
                onChangeBillingState={this.onChangeBillingState}
                onChangeBillingZip={this.onChangeBillingZip}
                db={this.props.dbClient}
              />
            </div>
            <ul className="nav">
              <h3 className="links">Active</h3>
              <h4 className="links">Inactive</h4>
            </ul>

            <div className="storage-container">
              <div className="card-leaderboard-labels">
                <h3 style={{ fontWeight: "300", fontSize: "24px" }}>
                  {this.state.clients.length} Active Clients
                </h3>
                <h3>
                  Name{" "}
                  <span onClick={this.sortByNameLast}>
                    <FaAngleDown color={"#888"} size={15} />
                  </span>
                </h3>
                <h3>
                  Email{" "}
                  <span onClick={this.sortByEmail}>
                    <FaAngleDown color={"#888"} size={15} />
                  </span>
                </h3>
                <h3>
                  Teams{" "}
                  <span>
                    <FaAngleDown color={"#888"} size={15} />
                  </span>
                </h3>
              </div>
              {this.clientList()}
            </div>
            <div className="storage-container">
              <div className="card-leaderboard-labels">
                <h3 style={{ fontWeight: "300", fontSize: "24px" }}>
                  {this.state.inactiveClients.length} Inactive Clients
                </h3>
                <h3>
                  Name{" "}
                  <span>
                    <FaAngleDown color={"#888"} size={15} />
                  </span>
                </h3>
                <h3>
                  Email{" "}
                  <span>
                    <FaAngleDown color={"#888"} size={15} />
                  </span>
                </h3>
                <h3>
                  Teams{" "}
                  <span>
                    <FaAngleDown color={"#888"} size={15} />
                  </span>
                </h3>
              </div>
              {this.inactiveClientList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Clients(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <ClientsClass
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
