import React, { Component } from "react";
import StarRating from "./starRating";
import { RiTimerLine } from "react-icons/ri";
import { BsQuestionCircle } from "react-icons/bs";
import { FaAngleDown } from "react-icons/fa";
import { AiOutlineStar } from "react-icons/ai";
import "../stylesheets/play.css";
import "../stylesheets/finishGame.css";

import "../stylesheets/App.css";
import "../stylesheets/specifics.css";

class FinishGame extends Component {
  constructor(props) {
    super(props);
    const {
      correct,
      total,
      score,
      time,
      scores,
      users,
      dbRating,
      history
    } = this.props;
    this.goBack = this.goBack.bind(this);
    this.getPercent = this.getPercent.bind(this);
    this.getLeaderboard = this.getLeaderboard.bind(this);
  }

  goBack() {
    this.props.history.goBack();
  }

  getPercent() {
    return Math.round((this.props.correct / this.props.total) * 100);
  }

  getLeaderboard() {
    if (this.props.users) {
      return this.props.users.slice(0, 5).map((user, i) => {
        return (
          <div className="card-leaderboard-users" key={i}>
            <h3 style={{ textAlign: "left", margin: '0 0 0 1.25rem'}}>{this.props.users[i].userName}</h3>
            <h3>
              {Math.round(this.props.users[i].time) < 60
                ? Math.round(this.props.users[i].time) + " sec"
                : Math.round(this.props.users[i].time / 60) + " min"}
            </h3>
            <h3>
              {this.props.users[i].correct} / {this.props.users[i].questions}
            </h3>
            <h3>{this.props.users[i].score}</h3>
          </div>
        );
      });
    }
  }

  render() {
    return (
      <div>
        <div className="result-rating">
          <h4 className="result-label">Your results</h4>
          <div className="rating-container">
            <h5>Rate this quiz!</h5>
            <StarRating
              dbRating={this.props.dbRating}
              history={this.props.history}
              color={this.props.star}
              location={this.props.location}
              client={this.props.client}
            />
          </div>
        </div>
        <div className="stats">
          <div className="stat">
            <h3>Total Time</h3>
            <h4 style={{ color: this.props.color }}>
              {Math.round(this.props.time / 60)}
              <span style={{ color: this.props.color }}>min</span>
            </h4>
          </div>

          <div className="stat">
            <h3>% Answered Correctly</h3>
            <h4 style={{ color: this.props.color }}>{this.getPercent()}%</h4>
          </div>

          <div className="stat">
            <h3>Total Score</h3>
            <h4 style={{ color: this.props.color }}>{this.props.score}</h4>
          </div>
        </div>
        <div className="storage-container">
          <div className="card-leaderboard-labels">
            <h3 style={{ fontWeight: "300", fontSize: "1.5rem" }}>
              Leaderboard
            </h3>
            <h3 style={{ transform: "translate(-2.5rem)" }}>
              <RiTimerLine color={this.props.color} size={"2rem"} />
              <span className="label-icon">ACTUAL COMPLETION TIME</span>
              <span>
                <FaAngleDown color={"#888"} size={15} />
              </span>
            </h3>
            <h3 style={{ transform: "translate(-2.5rem)" }}>
              <BsQuestionCircle
                color={this.props.color}
                size={"2rem"}
              />
              <span className="label-icon">QUESTIONS CORRECT</span>
              <span>
                <FaAngleDown color={"#888"} size={15} />
              </span>
            </h3>
            <h3 style={{ transform: "translate(-2.5rem)" }}>
              <AiOutlineStar color={this.props.color} size={"2rem"} />
              <span className="label-icon">TOTAL SCORE</span>
              <span>
                <FaAngleDown color={"#888"} size={15} />
              </span>
            </h3>
          </div>
          <div className="card-leaderboard" style={{margin: '1rem 0 0 0'}}>{this.getLeaderboard()}</div>
        </div>
      </div>
    );
  }
}

export default FinishGame;
