import React from "react";
import { Link } from "react-router-dom";
import { Switch } from "antd";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class TeamCard extends React.Component {
  constructor(props) {
    super(props);
    this.changeActive.bind(this);
    this.state = {
      doc: "",
      games: null,
      users: null,
      team: "",
      activeTeam: null
    };

    this.getTeamById = this.getTeamById.bind(this);
  }

  componentDidMount = () => {
    this.getTeamById();
    this.fetchGames();
    this.getUsers();
  };

  getTeamById = () => {
    this.props.dbTeam
      .find({
        selector: {
          _id: this.props.team._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows,
          activeTeam: rows[0].activeTeam
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchGames = () => {
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.props.team._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          games: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUsers = () => {
    this.props.dbUser
      .find({
        selector: {
          parentTeam: this.props.team._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          users: rows
        });
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  changeActive = () => {
    const a = !this.state.activeTeam;
    const f = this.props.dbTeam;
    const g = this.props.team._id;
    f.get(g)
      .then(function(doc) {
        doc.activeTeam = a;
        return f.put(doc);
      })
      .then(function() {
        // fetch data again
        return f.get(g);
      })
      .then(function(doc) {
        console.log(doc);
      });
  };

  rating() {
    var count = 0;
    this.state.rating.map((rating, i) => {
      return (count += rating.rating);
    });
    if (this.state.rating.length === 0) {
      return 0;
    } else {
      return count / this.state.rating.length;
    }
  }

  timeEstimation() {
    return (
      Number(this.state.doc[0].timePerQuestion) *
      Number(this.state.questions ? this.state.questions.length : 0)
    );
  }

  render() {
    if (this.state.doc === "" || this.state.rating === null) {
      return null;
    } else {
      return (
        <div>
          <div className="card">
            <div className="card-stats">
              <div className="card-stats-title">
                <h3
                  style={{
                    color: this.props.color,
                    fontSize: "26px",
                    fontWeight: "500"
                  }}
                >
                  <Link
                    to={"/editTeam/games/" + this.props.team._id}
                    style={{ textDecoration: "none", color: this.props.color }}
                  >
                    {this.props.team.name}
                  </Link>
                </h3>
                <span className="sub-stat">
                  {this.props.active ? "Active" : "Inactive"}
                </span>
              </div>
              <h3 className="team-active">
                <span>Active</span>
                <Switch
                  checked={this.props.active}
                  onChange={this.changeActive}
                />
              </h3>
              <h3>{this.state.games ? this.state.games.length : 0}</h3>
              <h3>{this.state.users ? this.state.users.length : 0}</h3>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default TeamCard;
