import React from "react";
import { Link } from "react-router-dom";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class TeamUserCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      questions: null,
      team: "",
      games: null,
      scoreDocs: null,
      gamesPlayed: null,
      totScore: 0,
      avgTime: 0,
      totalScore: null,
      totTime: null,
      totQ: null,
      avgTime: null,
      percentCorrect: null,
      questionsCorrect: null
    };

    this.getUserById = this.getUserById.bind(this);
    this.getScoreDocs = this.getScoreDocs.bind(this);
    this.getTotalScore = this.getTotalScore.bind(this);
    this.reducerScore = this.reducerScore.bind(this);
    this.reducerTime = this.reducerTime.bind(this);
    this.reducerQuestions = this.reducerQuestions.bind(this);
    this.reducerCorrect = this.reducerCorrect.bind(this);
    this.getTotTime = this.getTotTime.bind(this);
    this.getTotQuestions = this.getTotQuestions.bind(this);
    this.getQuestionCorrect = this.getQuestionCorrect.bind(this);
  }

  componentDidMount = () => {
    this.getUserById();
    this.getGames();
    this.getScoreDocs();
  };

  getUserById = () => {
    this.props.dbUser
      .find({
        selector: {
          parentTeam: this.props.teamId
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
        console.log(this.state.doc);
        console.log(this.props.teamId);
      })
      .catch(err => {
        console.log(err);
      });
    this.fetchTeam();
  };

  fetchTeam = () => {
    this.props.dbTeam
      .find({
        selector: {
          _id: this.props.user.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          team: rows
        });

        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getGames = () => {
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.props.user.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          games: rows
        });
        console.log(this.state.games);
      })
      .catch(err => {
        console.log(err);
      });
  };

  async getScoreDocs() {
    await this.props.dbScore.find({
      selector: {
        user: this.props.user._id
      }
    }).then(res => {
        const rows = res.docs;
            this.setState({
              scoreDocs: rows,
              gamesPlayed: rows.length,
            });
            console.log(this.state.scoreDocs);
            console.log(this.props.user._id);
            console.log(this.props);
          })
          .catch(err => {
            console.log(err);
          });

          this.getTotalScore();
          this.getTotTime();
          this.getTotQuestions();
          this.getQuestionCorrect();
      }

      reducerScore = (accum, item) => {
          if(accum.score == NaN|| item.score == NaN ){
            return;
          }
          else{
          return Number(accum) + Number(item.score);
        }
        };

        reducerTime = (accum, item) => {
          if(accum.time == NaN|| item.time == NaN ){
            return;
          }
          else{
          return Number(accum) + Number(item.time);
        }
        }

        reducerQuestions = (accum, item) => {
          if(accum.questions == NaN|| item.questions == NaN ){
            return;
          }
          else{
          return Number(accum) + Number(item.questions);
        }
        }

        reducerCorrect = (accum, item) => {
          if(accum.correct == NaN|| item.correct == NaN ){
            return;
          }
          else{
          return Number(accum) + Number(item.correct);
        }
        }

        getTotalScore() {
          const total  = Number(this.state.scoreDocs.reduce(this.reducerScore, 0));
          this.setState({
            totalScore: total
          });
        };

        getTotTime() {
          const total = Number(this.state.scoreDocs.reduce(this.reducerTime, 0));
          this.setState({
            totTime: total
          });
        };

        getTotQuestions() {
          const total = Number(this.state.scoreDocs.reduce(this.reducerQuestions, 0));
          this.setState({
            totQ: total,
            avgTime: this.state.totTime / total
          });
        };

        getQuestionCorrect() {
          const total = Number(this.state.scoreDocs.reduce(this.reducerCorrect, 0));
          this.setState({
            questionsCorrect: total,
            percentCorrect: (total/this.state.totQ)*100
          });
        };



  render() {
    if (this.state.doc === "") {
      return null;
    } else {
      return (
        <div>
          <Link
            to={"/profile/" + this.props.user._id}
            style={{ textDecoration: "none" }}
          >
            <div className="card">
              <div className="card-stats">
                <h3
                  style={{
                    color: this.props.color,
                    fontSize: "26px",
                    fontWeight: "500"
                  }}
                >
                  {this.props.user.firstname} {this.props.user.lastname}
                </h3>
                <h3>{this.state.gamesPlayed}</h3>
                <h3>{this.state.totalScore}</h3>
                <h3>{this.state.avgTime ? this.state.avgTime + " sec": "N/A"}</h3>
                <h3>{this.state.games ? this.state.games.length : 0}</h3>
              </div>
            </div>
          </Link>
        </div>
      );
    }
  }
}

export default TeamUserCard;
