import React, { Component } from "react";
import { useContext } from "react";
import { RiTimerLine } from "react-icons/ri";
import { BsQuestionCircle } from "react-icons/bs";
import { FaAngleDown } from "react-icons/fa";
import { AiOutlineStar } from "react-icons/ai";
import { Link } from "react-router-dom";
import Header from "./header";
import UserContext from "./context/userContext";
import UnplayedGame from "./unplayedGames";
import GameOpen from "./gameOpen";
import GameClosed from "./gameClosed";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class PlayHomeClass extends Component {
  constructor(props) {
    super(props);
    this.fetchGames.bind(this);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);
    this.cancel = this.cancel.bind(this);

    this.state = {
      parentClient: this.props.location,
      userData: null,
      unplayedGames: null,
      completedGames: null,
      clientName: "",
      active: false,
      header: "",
      activeColor: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchClient();
    this.getUser();

    this.canceler = this.props.dbGame
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchGames();
      });

    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }
  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.setState({
      clientName: ""
    });
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0];
        if (rows.logo) {
          var url = URL.createObjectURL(rows.logo);
        }
        this.setState({
          clientName: rows.nameClient,
          header: rows.header.hex,
          activeColor: rows.active.hex,
          secondary: rows.secondary.hex,
          background: rows.background.hex,
          headerImage: url
        });
        console.log(this.state.clientName);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchGames = () => {
    let completed = [];
    let uncompleted = [];
    this.setState({
      unplayedGames: null
    });
    this.setState({
      completedGames: null
    });
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.state.userData.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs;
        uncompleted = rows.slice();
        console.log(uncompleted);
      })
      .catch(err => {
        console.log(err);
      });
    this.props.dbScore
      .find({
        selector: {
          user: this.state.userData._id
        }
      })
      .then(res => {
        const data = res.docs;
        completed = data.slice();
        console.log(completed);
        console.log(uncompleted);

        for (var i = 0; i < completed.length; i++) {
          for (var j = 0; j < uncompleted.length; j++) {
            if (completed[i].gameId === uncompleted[j]._id) {
              uncompleted.splice(j, 1);
            }
          }
        }

        this.setState({
          unplayedGames: uncompleted,
          completedGames: completed
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
    setTimeout(() => {
      this.fetchGames();
    }, 1000);
  }

  completedGamesList() {
    return this.state.completedGames.map((game, i) => (
      <GameClosed
        key={i}
        _id={game.gameId}
        dbGame={this.props.dbGame}
        dbQuestion={this.props.dbQuestion}
        dbScore={this.props.dbScore}
        active={this.state.active}
        cancel={this.cancel}
        color={this.state.activeColor}
      />
    ));
  }

  unplayedGamesList() {
    return this.state.unplayedGames.map((game, i) => (
      <UnplayedGame
        key={i}
        _id={game._id}
        dbGame={this.props.dbGame}
        dbQuestion={this.props.dbQuestion}
        dbScore={this.props.dbScore}
        active={this.state.active}
        cancel={this.cancel}
        color={this.state.activeColor}
      />
    ));
  }

  cancel = () => {
    this.setState({
      active: false
    });
  };

  render() {
    if (
      this.state.unplayedGames === null ||
      this.state.completedGames === null
    ) {
      return null;
    } else if (this.state.userData) {
      return (
        <div>
          <Header
            admin={
              this.state.userData.admin || this.state.userData.teamEditor
                ? "/teams/" + this.state.parentClient
                : this.state.userData.gameEditor
                ? "/games/" + this.state.parentClient
                : this.state.userData.themeEditor
                ? "/themes/" + this.state.parentClient
                : this.state.userData.analyticsViewer
                ? "/editTeams/users/" + this.state.parentClient
                : null
            }
            game={this.props.location}
            background={this.state.header}
            image={this.state.headerImage}
            profile={"/profile/" + this.state.userData._id}
          />
          <div className="info"></div>
          <div className="info title" style={{ marginTop: "30px" }}>
            <h3 style={{ margin: "0" }}>Hello</h3>
          </div>
          {this.state.unplayedGames ? (
            <div
              className="banner"
              style={{ background: this.state.activeColor }}
            >
              <h5>
                You have{" "}
                {this.state.unplayedGames ? this.state.unplayedGames.length : 0}{" "}
                new quizzes to complete!
              </h5>
            </div>
          ) : null}
          <div className="info add-container" style={{ marginTop: "0" }}>
            <h3 style={{ margin: "0" }}>{this.state.userData.firstname}</h3>
          </div>
          <div className="storage-container">
            <div className="card-leaderboard-labels">
              <h3 style={{ fontWeight: "300", fontSize: "1.5rem" }}>
                {this.state.unplayedGames ? this.state.unplayedGames.length : 0}{" "}
                QUIZZES TO COMPLETE
              </h3>
              <h3 style={{ transform: "translate(-2.5rem)" }}>
                <RiTimerLine color={this.state.activeColor} size={"2rem"} />
                <span className="label-icon">ESTIMATED TIME</span>
                <span>
                  <FaAngleDown color={"#888"} size={15} />
                </span>
              </h3>
              <h3 style={{ transform: "translate(-2.5rem)" }}>
                <BsQuestionCircle
                  color={this.state.activeColor}
                  size={"2rem"}
                />
                <span className="label-icon">QUESTIONS</span>
                <span>
                  <FaAngleDown color={"#888"} size={15} />
                </span>
              </h3>
              <h3 style={{ transform: "translate(-2.5rem)" }}>
                <AiOutlineStar color={this.state.activeColor} size={"2rem"} />
                <span className="label-icon">TOTAL SCORE</span>
                <span>
                  <FaAngleDown color={"#888"} size={15} />
                </span>
              </h3>
            </div>
            {this.unplayedGamesList()}
          </div>
          <div className="storage-container">
            <div className="card-leaderboard-labels">
              <h3 style={{ fontWeight: "300", fontSize: "1.5rem" }}>
                {this.state.completedGames
                  ? this.state.completedGames.length
                  : 0}{" "}
                COMPLETED QUIZZES
              </h3>
              <h3 style={{ transform: "translate(-2.5rem)" }}>
                <RiTimerLine color={this.state.activeColor} size={"2rem"} />
                <span className="label-icon">ACTUAL COMPLETION TIME</span>
                <span>
                  <FaAngleDown color={"#888"} size={15} />
                </span>
              </h3>
              <h3 style={{ transform: "translate(-2.5rem)" }}>
                <BsQuestionCircle
                  color={this.state.activeColor}
                  size={"2rem"}
                />
                <span className="label-icon">QUESTIONS CORRECT</span>
                <span>
                  <FaAngleDown color={"#888"} size={15} />
                </span>
              </h3>
              <h3 style={{ transform: "translate(-2.5rem)" }}>
                <AiOutlineStar color={this.state.activeColor} size={"2rem"} />
                <span className="label-icon">TOTAL SCORE</span>
                <span>
                  <FaAngleDown color={"#888"} size={15} />
                </span>
              </h3>
            </div>
            {this.completedGamesList()}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default function PlayHome(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <PlayHomeClass
        location={props.location.pathname.replace("/playHome/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
