import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./header";


const Team = props => (
  <Link to={"/editTeam/games/" + props.team._id}>
    <div className="card">
      <div className="content">
        <span className="card-name">{props.team.name}</span>
        <span className="profile_userCount">{props.userCount}</span>
        <span className="profile_quizCount">{props.quizCount}</span>
      </div>
    </div>
  </Link>
);

class ProfileTeam extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user_id: props.location.pathname.replace("/profileTeam/", ""),
      team_id: null,
      client_id: null,
      teams: null,
      userCount: null,
      quizCount: null,
      clientName: null,
      firstname: "",
      lastname: ""
    }
    this.getUserTeam = this.getUserTeam.bind(this);
    this.getTeams = this.getTeams.bind(this);
    this.getUserCount = this.getUserCount.bind(this);
    this.getQuizCount = this.getQuizCount.bind(this);
    this.getClientName = this.getClientName.bind(this);
  }

  componentDidMount() {
    this.mounted = true;
    this.getUserTeam();
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUserTeam();
      });
      this.canceler = this.props.dbTeam
        .changes({
          since: "now",
          live: true,
          include_docs: true
        })
        .on("change", () => {
          this.getTeams();
        });
        this.canceler = this.props.dbGame
          .changes({
            since: "now",
            live: true,
            include_docs: true
          })
          .on("change", () => {
            this.getTeams();
          });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  getUserTeam(){
    this.props.dbUser.find({
      selector: {
        _id: this.state.user_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        team_id: rows[0].parentTeam,
        client_id: rows[0].parentClient,
        firstname: rows[0].firstname,
        lastname: rows[0].lastname
      });
      console.log(this.state.team_id)
    }).catch(err => {
      console.log(err);
    });
    setTimeout(function() { //Start the timer
    this.getTeams();
    this.getClientName(); //After 1 second, set render to true
}.bind(this), 1000)

  }

  getTeams() {

    this.props.dbTeam.find({
      selector: {
        _id: this.state.team_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        teams: rows
      });
      console.log(this.state.teams);
    }).catch(err => {
      console.log(err);
    });
    setTimeout(function() { //Start the timer
    this.getUserCount();
    this.getQuizCount(); //After 1 second, set render to true
}.bind(this), 100)
  }

  getUserCount() {
    this.props.dbUser.find({
      selector: {
        parentTeam: this.state.team_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        userCount: rows.length
      })
    }).catch(err => {
      console.log(err);
    });
  };

  getQuizCount() {
    this.props.dbGame.find({
      selector: {
        parentTeam: this.state.team_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        quizCount: rows.length
      })
    }).catch(err => {
      console.log(err);
    });
  };

  getClientName() {
    this.props.dbClient.find({
      selector: {
        _id: this.state.client_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        clientName: rows[0].nameClient
      })
    }).catch(err => {
      console.log(err);
    });
  }

  teamList() {
    return this.state.teams.map((team, i) => <Team team={team} key={i} userCount={this.state.userCount} quizCount={this.state.quizCount}/>);
  }

  render() {

    if (this.state.teams === null) {
      return null;
    }
    else{
    return (
      <div>
        <div>
          <Header
            admin={this.props.location}
            game={"/playHome/" + this.state.team_id}
            profile={"/profile/" + this.state.user_id}
          />
        </div>
        <div className = "profile_client_div">
          <p className="profile_client_text">{this.state.clientName} Users</p>
        </div>
        <div className="profile_client_name">
          <p className="profile_client_username">{this.state.firstname} {this.state.lastname}</p>
        </div>
        <div className="profile_nav">
        <Link to={"/profileAdmin/" + this.state.user_id}>
          <h3 className = "profile_nav_title">Profile</h3>
        </Link>
        <Link to={"/profileTeam/" + this.state.user_id}>
          <h3 className = "profile_nav_title">Teams</h3>
        </Link>
        <Link to={"/profileQuiz/" + this.state.user_id}>
          <h3 className= "profile_nav_title">Quizzes</h3>
        </Link>
        </div>
        <div className="userCard_header">
          <p className="team_active_label">1 Active Team</p>
          <p className="team_label_users"> USERS </p>
          <p className="team_label_quizes"> QUIZES </p>
        </div>
        <div>
          {this.teamList()}
        </div>
      </div>
    )
  }
}

}

export default ProfileTeam;
