import React from "react";
import { Link } from "react-router-dom";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class ClientCard extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      teams: null,
      client: ""
    };

    this.getClientById = this.getClientById.bind(this);
  }

  componentDidMount = () => {
    this.getClientById();
    this.fetchTeams();
  };

  getClientById = () => {
    this.props.dbClient
      .find({
        selector: {
          _id: this.props.client._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchTeams = () => {
    this.props.dbTeam
      .find({
        selector: {
          parentClient: this.props.client._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          teams: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  render() {
    if (this.state.doc === "") {
      return null;
    } else {
      return (
        <div>
          <Link
            to={"/contact/" + this.props.client._id}
            style={{ textDecoration: "none" }}
          >
            <div className="card">
              <div className="card-stats">
                <div className="card-stats-title">
                  <h3
                    style={{
                      color: this.props.color,
                      fontSize: "26px",
                      fontWeight: "500"
                    }}
                    className={this.props.inactive ? "inactive" : null}
                  >
                    {this.props.client.nameClient}
                  </h3>
                  <span className="sub-stat">
                    {this.props.inactive ? "Inactive" : "Active"}
                  </span>
                </div>
                <h3 className={this.props.inactive ? "inactive" : null}>
                  {this.props.client.nameFirst} {this.props.client.nameLast}
                </h3>
                <h3 className={this.props.inactive ? "inactive" : null}>
                  {this.props.client.email}
                </h3>
                <h3 className={this.props.inactive ? "inactive" : null}>
                  {this.state.teams ? this.state.teams.length : 0}
                </h3>
              </div>
            </div>
          </Link>
        </div>
      );
    }
  }
}

export default ClientCard;
