import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import { Multiselect } from "multiselect-react-dropdown";
import "./stylesheets/profile.css";

class ProfileAdmin extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user_id: props.location.pathname.replace("/profileAdmin/", ""),
      totalScore: null,
      scoreDocs: null,
      gamesPlayed: null,
      avgTimeAnswer: null,
      user: null,
      username: "",
      lastname: "",
      firstname: "",
      email: "",
      admin: null,
      analyticsViewer: null,
      gameEditor: null,
      teamEditor: null,
      themeEditor: null,
      playerOnly: null,
      options: [
        {name: "Admin", id: "admin"},
        {name: "Analytics Viewer", id: "analyticsViewer"},
        {name: "Game Editor", id: "gameEditor"},
        {name: "Team Editor", id: "teamEditor"},
        {name: "Theme Editor", id: "themeEditor"},
        {name: "Player Only", id: "playerOnly"},
      ],
      selectedPermissions: [],
      image: "",
      totalScore: null,
      totTime: null,
      totQ: null,
      avgTime: null,
      percentCorrect: null,
      questionsCorrect: null,
      clientName: null,
      title: null
    };
    this.getScoreDocs = this.getScoreDocs.bind(this);
    this.getSelectedPermissions = this.getSelectedPermissions.bind(this);
    this.getUser = this.getUser.bind(this);
    this.onSelect = this.onSelect.bind(this);
    this.getTotalScore = this.getTotalScore.bind(this);
    this.reducerScore = this.reducerScore.bind(this);
    this.reducerTime = this.reducerTime.bind(this);
    this.reducerQuestions = this.reducerQuestions.bind(this);
    this.reducerCorrect = this.reducerCorrect.bind(this);
    this.getTotTime = this.getTotTime.bind(this);
    this.getTotQuestions = this.getTotQuestions.bind(this);
    this.getQuestionCorrect = this.getQuestionCorrect.bind(this);
    this.getClientName = this.getClientName.bind(this);
  }

  componentDidMount() {
    this.mounted = true;
    this.getScoreDocs();
    this.getUser();
    this.canceler = this.props.dbScore
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getScoreDocs();
      });

      this.canceler = this.props.dbUser
        .changes({
          since: "now",
          live: true,
          include_docs: true
        })
        .on("change", () => {
          this.getUser();
        });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  async getScoreDocs() {
    await this.props.dbScore.find({
      selector: {
        user: this.state.user_id
      }
    }).then(res => {
        const rows = res.docs;
            this.setState({
              scoreDocs: rows,
              gamesPlayed: rows.length,

            });
          })
          .catch(err => {
            console.log(err);
          });
          this.getTotalScore();
          this.getTotTime();
          this.getTotQuestions();
          this.getQuestionCorrect();
      }

  async getUser() {
    await this.props.dbUser.find({
      selector: {
        _id: this.state.user_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        user: rows,
        username: rows[0].username,
        email: rows[0].email,
        firstname: rows[0].firstname,
        lastname: rows[0].lastname,
        admin: rows[0].admin,
        analyticsViewer: rows[0].analyticsViewer,
        gameEditor: rows[0].gameEditor,
        teamEditor: rows[0].teamEditor,
        themeEditor: rows[0].themeEditor,
        playerOnly: rows[0].playerOnly,
        image: rows[0].image,
        title: rows[0].title
      });
      console.log(this.state.user);
      this.getSelectedPermissions();
    }).catch(err => {
      console.log(err);
    });
    this.getClientName();
  };

  getClientName() {
    this.props.dbClient.find({
      selector: {
        _id: this.state.user[0].parentClient
      }
    }).then(res => {
      const rows = res.docs;
      console.log(res.docs);
      this.setState({
        clientName: rows[0].nameClient
      })
    })
  }

  reducerScore = (accum, item) => {
      if(accum.score == NaN|| item.score == NaN ){
        return;
      }
      else{
      return Number(accum) + Number(item.score);
    }
    };

    reducerTime = (accum, item) => {
      if(accum.time == NaN|| item.time == NaN ){
        return;
      }
      else{
      return Number(accum) + Number(item.time);
    }
    }

    reducerQuestions = (accum, item) => {
      if(accum.questions == NaN|| item.questions == NaN ){
        return;
      }
      else{
      return Number(accum) + Number(item.questions);
    }
    }

    reducerCorrect = (accum, item) => {
      if(accum.correct == NaN|| item.correct == NaN ){
        return;
      }
      else{
      return Number(accum) + Number(item.correct);
    }
    }

    getTotalScore() {
      const total  = Number(this.state.scoreDocs.reduce(this.reducerScore, 0));
      this.setState({
        totalScore: total
      });
    };

    getTotTime() {
      const total = Number(this.state.scoreDocs.reduce(this.reducerTime, 0));
      this.setState({
        totTime: total
      });
    };

    getTotQuestions() {
      const total = Number(this.state.scoreDocs.reduce(this.reducerQuestions, 0));
      this.setState({
        totQ: total,
        avgTime: this.state.totTime / total
      });
    };

    getQuestionCorrect() {
      const total = Number(this.state.scoreDocs.reduce(this.reducerCorrect, 0));
      this.setState({
        questionsCorrect: total,
        percentCorrect: (total/this.state.totQ)*100
      });
    };

  getSelectedPermissions() {
    if(this.state.admin === true){
      const added = [{name: "Admin", id: "admin"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.analyticsViewer === true){
      const added = [{name: "Analytics Viewer", id: "analyticsViewer"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.gameEditor === true){
      const added = [{name: "Game Editor", id: "gameEditor"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.teamEditor === true){
      const added = [{name: "Team Editor", id: "teamEditor"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.themeEditor === true){
      const added = [{name: "Theme Editor", id: "themeEditor"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.playerOnly === true){
      const added = [{name: "Player Only", id: "playerOnly"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
  }

  async onSelect(selectedList, selectedItem) {
    const db = this.props.dbUser;
    const user_id = this.state.user_id;
     if(selectedItem.id === "admin"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.admin = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "analyticsViewer"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.analyticsViewer = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "gameEditor"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.gameEditor = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "teamEditor"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.teamEditor = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "themeEditor"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.themeEditor = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "playerOnly"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.playerOnly = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }
    window.location.reload(true);
  }

  async onRemove(selectedList, removedItem) {
    const db = this.props.dbUser;
    const user_id = this.state.user_id;
    if(removedItem.id === "admin"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.admin = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "analyticsViewer"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.analyticsViewer = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "gameEditor"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.gameEditor = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "teamEditor"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.teamEditor = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "themeEditor"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.themeEditor = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "playerOnly"){
      await this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.playerOnly = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }
    window.location.reload(true);
  }

  render() {

    if (this.state.user === null) {
      return null;
    }
    else{
    return (
      <div>
        <Header
          admin={this.props.location}
          game={"/playHome/" + this.state.user[0].parentClient}
          profile={"/profile/" + this.state.user_id}
        />
      {/** back button **/}
        <Link className="info back" to={"/playHome/" + this.state.user[0].parentClient}>
          <h3>&larr; Back to Games</h3>
        </Link>
        <div className = "profile_client_div">
          <p className="profile_client_text">{this.state.clientName} Users</p>
        </div>
        <div className="profile_client_name">
          <p className="profile_client_username">{this.state.firstname} {this.state.lastname}</p>
        </div>
        <div className="profile_nav">
        <Link to={"/profileAdmin/" + this.state.user_id}>
          <h3 className = "profile_nav_title">Profile</h3>
        </Link>
        <Link to={"/profileTeam/" + this.state.user_id}>
          <h3 className = "profile_nav_title">Teams</h3>
        </Link>
        <Link to={"/profileQuiz/" + this.state.user_id}>
          <h3 className= "profile_nav_title">Quizzes</h3>
        </Link>
        </div>

          <div className="profile_top">
            <img alt="profile_pic" src={this.state.image} style={{height: 100, width: 100}}/>
            <div className="profile_top_name_role">
            <p className="profile_name">{this.state.firstname} {this.state.lastname} </p>
            <p className="profile_role"> {this.state.title} </p>
            </div>
            <div className="profile_top_email_admin">
            <p className="profile_top_label">EMAIL </p>
            <p className="profile_top_text"> {this.state.email} </p>
            </div>
            <div className="profile_top_user_admin">
            <p className="profile_top_label">USERNAME </p>
            <p className="profile_top_text"> {this.state.username} </p>
            </div>
            <div className="profile_permissions">
            <p className="profile_top_label">USER TYPE </p>
            <Multiselect
            options={this.state.options}
            selectedValues={this.state.selectedPermissions}
            onSelect={this.onSelect.bind(this)}
            onRemove={this.onRemove.bind(this)}
            displayValue="name"
            style={
              {
                multiselectContainer: {width: 200, marginLeft: 100, border: "none" },
                option: {color: "white", border: "none"},
                optionContainer: {border: "none"},
                chips: {color: "white", border: "none"},
                searchBox: {border: "none"},
              }
            }
            />
            </div>
        </div>
        <div className="profile_score_div">
        {/** all time score **/}
          <div className="profile_allTime">
            <p className="profile_allTime_label">All Time Score</p>
            <p className="profile_allTime_text">{this.state.totalScore}</p>
          </div>
          <div className="allTime_except">
            <div className="profile_totGames">
        {/** number of games played **/}
              <p className="profile_label">Total # of Games Played</p>
              <p className="profile_text">{this.state.gamesPlayed}</p>
            </div>
            <div className = "profile_avgTime">
        {/** average time to answer **/}
              <p className="profile_label">Average Time to Answer</p>
              <p className = "profile_text">{this.state.avgTime} sec</p>
            </div>
            <div className="profile_percent">
        {/** percent answered correctly **/}
              <p className="profile_label">Percentage Answered Correctly</p>
              <p className="profile_text">{this.state.percentCorrect}%</p>
            </div>
        </div>
        </div>
      </div>
    )
  }
  }
}

export default ProfileAdmin;
