import React, { useState, useContext, useEffect } from "react";
import { useHistory } from "react-router-dom";
import UserContext from "../../context/userContext";
import { AiOutlineUpload } from "react-icons/ai";
import axios from "axios";
import ErrorNotice from "../misc/ErrorNotice";
import "../../stylesheets/App.css";
import "../../stylesheets/Form.scss";
import emailjs from "emailjs-com";

export default function Register(props) {
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [passwordCheck, setPasswordCheck] = useState();
  const [userName, setUserName] = useState();
  const [firstName, setFirstName] = useState();
  const [lastName, setLastName] = useState();
  const [title, setTitle] = useState();
  const [error, setError] = useState();
  const [user, setUser] = useState();

  const { setUserData } = useContext(UserContext);
  const history = useHistory();

  useEffect(() => {
      getUser();
      setTitle('employee');
      if (user) {
        setEmail(user.email);
        setFirstName(user.firstname);
        setLastName(user.lastname)
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
  })

  const submit = async e => {
    e.preventDefault();

    try {
      const newUser = {
        email,
        password,
        passwordCheck,
        title,
        userName,
        firstName,
        lastName
      };
      await axios.post("ds227565.mlab.com:27565/heroku_gflx9gcm/users/register", newUser);
      const loginRes = await axios.post("ds227565.mlab.com:27565/heroku_gflx9gcm/users/login", {
        email,
        password
      });
      setUserData({
        token: loginRes.data.token,
        user: loginRes.data.user
      });
      localStorage.setItem("auth-token", loginRes.data.token);
      history.push("/");
    } catch (err) {
      err.response.data.msg && setError(err.response.data.msg);
    }
  };


const getUser = () => {
  try {
  props.dbUser
    .find({
      selector: {
        _id: props.location.pathname.replace("/register/", "")
      }
    })
    .then(res => {
      const rows = res.docs;
      setUser(rows[0])

    })
    .catch(err => {
      console.log(err);
    });
}
catch (err) {
  err.response.data.msg && setError(err.response.data.msg);
}
}

  return (
    <div className="root-container-register">
      <div className="box-container-register">
        <div className="inner-container-register">
          <div className="register-header">
            <h1>Create Account</h1>
          </div>
          <div className="box">
            {error && (
              <ErrorNotice
                message={error}
                clearError={() => setError(undefined)}
              />
            )}
            <form onSubmit={submit}>
              <div className="input-group-double-container">
                <div className="input-group-double">
                  <label htmlFor="register-firstname">First Name</label>
                  <h3>{user ? user.firstname : null}</h3>
                </div>
                <div className="input-group-double">
                  <label htmlFor="register-lastname">Last Name</label>
                  <h3>{user ? user.lastname : null}</h3>
                </div>
              </div>
              <div className="input-group-double-container">
                <div className="input-group-double">
                  <label htmlFor="register-user-name">Username</label>
                  <input
                    id="register-user-name"
                    className="login-input"
                    maxLength="20"
                    type="text"
                    onChange={e => setUserName(e.target.value)}
                  />
                  {userName ? (
                    <span>
                      {userName ? userName.length : 0} / 20 characters
                    </span>
                  ) : null}
                </div>
                <div className="input-group-double">
                  <label htmlFor="register-email">Email</label>
                  <h3>{user ? user.email : null}</h3>
                </div>
              </div>
              <div className="input-group-double-container">
                <div className="input-group-double">
                  <label htmlFor="register-password">Password</label>
                  <input
                    id="register-password"
                    className="login-input"
                    type="password"
                    onChange={e => setPassword(e.target.value)}
                  />
                </div>
                <div className="input-group-double">
                  <label htmlFor="register-confirm-password">
                    Confirm Password
                  </label>
                  <input
                    type="password"
                    className="login-input"
                    placeholder="Verify password"
                    onChange={e => setPasswordCheck(e.target.value)}
                  />
                </div>
              </div>
              <div className="input-group-photo">
                <label htmlFor="register-photo">Profile Picture</label>
                <div className="profile-photo-upload">
                  <span>
                    <AiOutlineUpload size={25} color={"#A8A8A8"} />
                  </span>
                  <h5 style={{ color: "#A8A8A8" }}>Upload Photo</h5>
                </div>
              </div>

              <input
                type="submit"
                className="register-btn"
                value="Create Account"
              />
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
