import React from "react";
import GameOpen from "./gameOpen";
import { FaAngleDown } from "react-icons/fa";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class GameClosed extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      active: false
    };

    this.getGameById = this.getGameById.bind(this);
    this.openGame = this.openGame.bind(this);
  }

  componentDidMount = () => {
    this.getGameById();
  };

  getGameById = () => {
    this.props.dbScore
      .find({
        selector: {
          gameId: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
        console.log(this.state.doc);
      })
      .catch(err => {
        console.log(err);
      });
  };

  openGame = () => {
    this.getGameById();
    this.setState({
      active: true
    });
  };

  render() {
    if (this.state.doc === "") {
      return null;
    } else if (this.state.active === true) {
      return (
        <GameOpen
          _id={this.props._id}
          game={this.state.doc[0]}
          dbGame={this.props.dbGame}
          dbScore={this.props.dbScore}
          cancel={this.props.cancel}
          color={this.props.color}
        />
      );
    } else {
      return (
        <div
          className="card"
          onClick={this.openGame}
          style={{ cursor: "pointer" }}
        >
          <div className="card-stats">
            <div className="card-stats-title">
              <h3
                style={{ color: this.props.color }}
                className="complete-title"
              >
                <span className="caret">
                  <FaAngleDown color={"#888"} size={20} />
                </span>
                <span>{this.state.doc[0].game}</span>
              </h3>
              <span className="complete">Complete</span>
            </div>
            <h3>
              {this.state.doc[0].time < 60
                ? this.state.doc[0].time + " sec"
                : Math.round(this.state.doc[0].time / 60) + " min"}
            </h3>
            <h3>{this.state.doc[0].correct}</h3>
            <h3>{this.state.doc[0].score}</h3>
          </div>
        </div>
      );
    }
  }
}

export default GameClosed;
